import { injectJs } from './document';

// Для нашего счетчика 26812653 в первой метрики есть условие, которое запрещает логирование с помощью _ym_debug=1
// Возможно проверить корректность с помощью скачанного локально кода первой метрики, удаления из него этого условия, проксирования запросов с https://mc.yandex.ru/metrika/watch.js на локальный скрипт
// https://st.yandex-team.ru/METRIKASUPP-11524

function createCounter(id, callback = () => {}) {
    try {
        const MetrikaConstructor = window.Ya.Metrika2 || window.Ya.Metrika;

        window[`yaCounter${id}`] = new MetrikaConstructor({
            id: id,
            trackLinks: true,
            accurateTrackBounce: true,
            params: {
                shareVersion: 2,
                redesign: 1,
                curtain: 1
            },
            triggerEvent: true
        });

        callback();
    } catch (_) {
        // ignore error
    }
}

/**
 * Ищет на странице подключенный скрипт метрики
 */
function hasMetrikaScriptInDOM() {
    const metrikaUrlRegExp = /^https:\/\/mc\.yandex\.ru\/metrika\/(?:watch|tag)\.js$/;
    const scriptTags = Array.prototype.slice.call(document.getElementsByTagName('script'));

    return scriptTags.some(node => metrikaUrlRegExp.test(node.src));
}

/**
 * Проверяет, инициализирована первая или вторая метрика на странице
 */
function hasMetrikaInWindow() {
    return window.Ya && ('Metrika' in window.Ya || 'Metrika2' in window.Ya);
}

function pushCallback(callback) {
    const namespace = 'yandex_metrika_callbacks2'; // Если инициализируем через pushCallback, то всегда грузится вторая версия

    window[namespace] = window[namespace] || [];
    window[namespace].push(callback);
}

class Metrika {
    constructor(id) {
        this._id = id;

        this.intervalTimer = null;
        this.timeoutTimer = null;
    }

    injectMetrikaScript() {
        const script = injectJs('https://mc.yandex.ru/metrika/tag.js');

        pushCallback(() => {
            createCounter(this._id, () => script && script.parentNode.removeChild(script));
        });
    }

    /**
     * Ждем, пока подгрузится имеющийся в DOM скрипт метрики
     */
    waitMetrikaLoading() {
        this.intervalTimer = setInterval(() => {
            // Если метрика инициализовалась, чистим все таймеры и создаем счетчик
            if (hasMetrikaInWindow()) {
                clearInterval(this.intervalTimer);
                clearTimeout(this.timeoutTimer);

                createCounter(this._id);
            }
        }, 45);

        this.timeoutTimer = setTimeout(() => {
            // Если метрика не инициализовалась спустя 400мс, инжектим скрипт загрузки
            clearInterval(this.intervalTimer);

            this.injectMetrikaScript();
        }, 400);
    }

    /**
     * Init Yandex.Metrika. Insert it into the page if not present
     */
    init() {
        if (hasMetrikaInWindow()) {
            return createCounter(this._id);
        }

        if (hasMetrikaScriptInDOM()) {
            return this.waitMetrikaLoading();
        }

        this.injectMetrikaScript();
    }

    /**
     * Get Yandex.Metrika counter
     *
     * @return {Object}
     */
    getCounter() {
        return window[`yaCounter${this._id}`];
    }
}

export default Metrika;
